<template>
  <div>
    <am-nav-bar
      title="Demo"
      :left-btn="[]"
    ></am-nav-bar>
    <!-- <div class="am-picker">
      <div
        ref="test"
        class="am-picker-col"
        @panstart="ontouchstart"
        @panmove="ontouchmove"
        @panend="ontouchend"
      >
        <div class="am-picker-col-placeholder"></div>
        <div :ref="`div${i}`" v-for="num in 100" :key="num">
          <text class="am-picker-col-item">{{num}}</text>
        </div>
        <div class="am-picker-col-placeholder"></div>
      </div>
    </div> -->

    <div class="am-picker">
      <scroller class="am-picker-col" @panstart="onscrollend" :offset-accuracy="1" @scroll="onscroll($event, i)" @scrollend="onscrollend" v-for="i in 2" :key="`col-${i}`">
        <div class="am-picker-col-placeholder"></div>
        <div :ref="`cell${i}`" v-for="num in 100" :key="num">
          <text class="am-picker-col-item">{{num}}</text>
        </div>
        <div class="am-picker-col-placeholder"></div>
        <!-- <div class="am-picker-col-mask-top"></div>
        <div class="am-picker-col-mask-bottom"></div> -->
      </scroller>
    </div>
    <text>{{values}}</text>
  </div>
</template>

<style lang="less">
@import '../theme/base/common.less';

@picker-item-height: 34 * @hd;

.am-picker {
  flex-direction: row;
  overflow: hidden;
  &-col {
    height: @picker-item-height * 7;
    align-self: flex-start;
    flex: 1;
    &-item {
      text-align: center;
      font-size: @font-size-caption;
      height: @picker-item-height;
      line-height: @picker-item-height;
      color: @color-text-base;
      white-space: nowrap;
      text-overflow: ellipsis;

      &-selected {
        font-size: @font-size-heading;
      }
    }
    &-placeholder {
      height: @picker-item-height * 3;
    }

    &-mask {
      &-top {
        top: 90px;
        position: fixed;
        left: 0;
        // top: 0;
        height: @picker-item-height * 3;
        right: 0;
        background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
        background-repeat: no-repeat;
      }
      &-bottom {
        top: 90px + @picker-item-height * 4;
        position: fixed;
        left: 0;
        // top: 0;
        height: @picker-item-height * 3;
        right: 0;
        background-image: linear-gradient(to top, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
        background-repeat: no-repeat;
      }
      // background-size: 100% @picker-item-height * 3;
    }
  }

}
</style>

<script>
// import { AmNavBar, AmList, AmListItem, Utils } from '../index'
import AmNavBar from '../packages/am-nav-bar'
import AmList from '../packages/am-list'
import AmListItem from '../packages/am-list-item'
import Utils from '../packages/utils'
const navigator = weex.requireModule('navigator')
const animation = weex.requireModule('animation')
const dom = weex.requireModule('dom')

// let cellHeight = null
export default {
  components: { AmNavBar, AmList, AmListItem },
  data () {
    return {
      test: '',
      top: 0,
      sid: 0,
      cellHeight: null,
      lastY: null,
      startY: null,
      lastMoveStart: null,
      lastMoveTime: null,
      stopInertiaMove: false
    }
  },
  // mounted () {
  //   if (!cellHeight) {
  //     cellHeight = Utils.isWeb() ? this.$refs.cell1[0].clientHeight : 68
  //   }
  //   this.cellHeight = cellHeight
  // },
  methods: {
    handleClick (name) {
      console.log(name)
      name = name.replace('am-', '')
      const suffix = Utils.isWeb() ? 'html' : 'js'
      let url = `${name}/index.${suffix}`
      if (/\/$/.test(weex.config.bundleUrl)) {
        url = weex.config.bundleUrl = url
      } else {
        url = weex.config.bundleUrl.replace(`index.${suffix}`, url)
      }
      console.log('url', url)
      navigator.push({
        url: url,
        animated: 'true'
      })
    },
    onappear ($event, index) {
      console.log('show', index, $event.direction)
      switch ($event.direction) {
        case 'up':
          this.i = index - 3
          break
        case 'down':
          this.i = index + 4
          break
      }
    },
    ondisappear ($event, index) {
      console.log('hide', index, $event.direction)
      if ($event.direction === 'up' && 100 - index < 7) {
        console.log(100 - index)
      }
      // console.log($event)
    },
    onscroll (event, i) {
      // console.log(event)
      clearTimeout(this.sid)
      this.sid = setTimeout(() => {
        let y = -event.contentOffset.y
        if (!this.cellHeight) {
          this.cellHeight = event.contentSize.height / (this.$refs[`cell${i}`].length + 6)
        }
        let remainder = y % this.cellHeight
        if (remainder === 0) return
        let index = Math.abs(Math.ceil(y / this.cellHeight)) - 1
        if (remainder > this.cellHeight / 2) {
          index++
        }
        dom.scrollToElement(this.$refs.cell1[index], {offset: -68 * 3})
      }, 500)
    },
    onscrollend (event) {
      this.test = event
      // this.test = event.contentOffset.y
    },
    ontouchstart (e) {
      // this.top = e
      this.lastY = this.startY = e.changedTouches[0].screenY

      /**
       * 缓动代码
       */
      this.lastMoveStart = this.lastY
      this.lastMoveTime = e.timeStamp || Date.now()
      this.stopInertiaMove = true
    },
    ontouchmove (e) {
      let nowY = e.changedTouches[0].screenY
      let moveY = nowY - this.lastY
      let contentTop = this.top
      // 设置top值移动content
      this.top = (parseInt(contentTop) + moveY)
      this.lastY = nowY

      /**
       * 缓动代码
       */
      let nowTime = e.timeStamp || Date.now()
      this.stopInertiaMove = true
      if (nowTime - this.lastMoveTime > 300) {
        this.lastMoveTime = nowTime
        this.lastMoveStart = nowY
      }
    },
    ontouchend (e) {
      let nowY = e.changedTouches[0].screenY
      let moveY = nowY - this.lastY
      let contentTop = this.top
      let contentY = (parseInt(contentTop) + moveY)
      // 设置top值移动content
      this.top = contentY
      this.lastY = nowY

      /**
       * 缓动代码
       */
      let nowTime = e.timeStamp || Date.now()
      let v = (nowY - this.lastMoveStart) / (nowTime - this.lastMoveTime) // 最后一段时间手指划动速度
      this.stopInertiaMove = false;
      ((v, startTime, contentY) => {
        let dir = v > 0 ? -1 : 1 // 加速度方向
        let deceleration = dir * 0.001
        let duration = v / deceleration // 速度消减至0所需时间
        let dist = v * duration / 2 // 最终移动多少
        console.log(duration)
        this.test = `translateY(${dist}px)${duration}`
        const el = this.$refs.test
        this.top = this.top + -dist
        let obj = {
          styles: {
            transform: `translateY(${Math.ceil(this.top)}px)`,
            transformOrigin: 'center center'
          },
          needLayout: false,
          delay: 0 // ms
        }

        // if (bool) {
        obj.duration = -duration
        obj.timingFunction = 'ease-out'
        // }
        animation.transition(el, obj)
        setTimeout(() => {
          obj.styles.transform = `translateY(0px)`
          obj.duration = 0
          animation.transition(el, obj)
        }, 500)
        // function inertiaMove () {
        //   if (this.stopInertiaMove) return
        //   let nowTime = e.timeStamp || Date.now()
        //   let t = nowTime - startTime
        //   let nowV = v + t * deceleration
        //   // 速度方向变化表示速度达到0了
        //   if (dir * nowV < 0) {
        //     return
        //   }
        //   let moveY = (v + nowV) / 2 * t
        //   this.top = (contentY + moveY)
        //   console.log(this.top)
        //   setTimeout(inertiaMove.bind(this), 10)
        // }
        // inertiaMove.bind(this)()
      })(v, nowTime, contentY)
    }
  }
}
</script>



